import rdSwipe from './vue-slide.vue'
export default {
  data () {
    return {
      swipe: {
        activeIndex: 0
      },
      imgs: [
        'img/banner_test.jpg',
        'img/round01.jpg',
        'img/round01.jpg',
        'img/round01.jpg'
      ]
    }
  },
  options: {
    activeIndex: 0,
    autoplay: 1000,
    pagination: true
  },
  components: {
    rdSwipe
  },
  methods: {
    turnTo (index) {
      this.$children.map(swipe => {
        if (swipe.turnTo) {
          swipe.turnTo(index)
        }
      })
    }
  }
}
