import {mapState, mapMutations} from 'vuex'
export default {
  props: ['indexClass'],
  computed: {
    ...mapState([
      'courseId', 'coursename', 'courseSort'
    ])
  },
  methods: {
    ...mapMutations([
      'COURSE_ID', 'COURSE_PARAMS', 'COURSE_SORT'
    ]),
    getCourseList (id, name) {
      if (name === '更多') {
        this.COURSE_ID('15963587')
        this.COURSE_PARAMS('分类')
        this.COURSE_SORT('7')
        this.COURSE_TYPE('1')
      } else {
        this.COURSE_ID(id)
        this.COURSE_PARAMS(name)
      }
    }
  }
}
